<template>
	<div id="goods">
		<div class="my-bar">
			<div class="my-bar-left">
				<div class="arrow-left-con">
					<van-icon name="arrow-left" @click.native="$router.go('-1')"/>
				</div>
			</div>
		</div>
		<van-swipe class="goods-swipe" :autoplay="3000">
			<van-swipe-item v-for="thumb,index in goodsInfo.goodslogo.split(',undefined')[0].split(',')" :key="index">
				<img :src="thumb" width="100%" height="240px" @click="aaaaa(index)">
			</van-swipe-item>
		</van-swipe>
		<van-cell-group>
			<van-cell>
				<div class="goods-title" style="text-align: left">
          <label>{{goodsInfo.goodsname}}</label>
          <!--<img src="static/img/icon_ms_xsmsspxq.png" style="width: 20px;vertical-align: middle;">-->
          <!--<label class="font_12 color_8b8b8b">倒计时</label>-->
          <!--<label style="vertical-align: middle;color: #ff9c00;font-weight: bold;">1:25:03</label>-->
        </div>
        <div class="font_12">
          <label class="color_8b8b8b">{{goodsInfo.introduce}}</label>
          <!--<br>-->
          <!--<label style="padding: 2px 10px;border-radius: 3px" class="background_color_c10804 color_ffffff">家具</label>-->
        </div>
				<div class="goods-price text_right" style="padding: 10px 0;">
          <label style="float: left" class="color_c10804">￥{{goodsInfo.price}}</label>
          <img v-if="goodsInfo.shop" :src="goodsInfo.shop.shoplogo" style="width: 20px;vertical-align: middle;" @click="toSeller(goodsInfo.shop)">
          <label class="color_c10804" v-if="goodsInfo.shop">{{goodsInfo.shop.shopname}}</label>
        </div>
        <div class="goods-price text_right" style="padding: 10px 0;">
          <label class="color_8b8b8b font_12" style="float: left">请选择购买数量</label>
          <label>
            <span class="minusNum" @click="minusNum">-</span>
            <span>{{goodsNum}}</span>
            <span class="addNum" @click="addNum">+</span>
          </label>
        </div>
			</van-cell>
		</van-cell-group>

		<van-cell-group class="goods-cell-group">
			<van-cell>
				<template slot="title">
					<span class="van-cell-text">评价({{evaluateList.length}})</span>
          <div v-for="(item,index) in evaluateList">
            <div>
              <img src="static/img/img_tx1_spxq.png" style="width: 30px;vertical-align: middle;">
              <label class="color_8b8b8b" style="padding-left: 10px">{{item.userid}}</label>
              <label class="font_12 color_8b8b8b">{{item.addtime}}</label>
              <label style="float: right;color: #fcab52">{{item.score}}分</label>
            </div>
            <div style="padding-top: 10px;line-height: 14px;">
              {{item.content}}
            </div>
            <div v-if="item.img">
              <img style="padding: 2px" v-for="(item1,index) in item.img.split(',')" :src="item1" :key="index" width="32%"/>
            </div>
          </div>
				</template>
			</van-cell>
		</van-cell-group>
    <div class="van-goods-action">
      <a class="van-hairline van-goods-action-mini-btn">
        <img src="static/img/icon_sc_spxq.png" class="goods-bottom" v-if="goodsInfo.iscollection=='N'" @click.stop="addCollect">
        <img src="static/img/icon_sc_pre_spxq.png" class="goods-bottom" v-if="goodsInfo.iscollection=='Y'" @click.stop="removeCollect">
        收藏
      </a>
      <a class="van-hairline van-goods-action-mini-btn" @click="copeText">
        <img src="static/img/icon_fx_spxq.png" class="goods-bottom">
        分享
      </a>
      <a v-if="goodsInfo.shop" class="van-hairline van-goods-action-mini-btn" @click="tel(goodsInfo.shop.tel)">
        <img src="static/img/icon_kefu_spxq.png" class="goods-bottom">
        客服
      </a>
      <a style="background-color: #fcab52" class="van-button van-button--default van-button--normal van-button--bottom-action van-goods-action-big-btn" @click="buyGoods">
        <span class="van-button__text">
          购买
        </span>
      </a>
    </div>
	</div>
</template>

<script>
  import { ImagePreview } from 'vant';
	export default {
		name: 'goods',
		data() {
			return {
			  goodsInfo:[],
        goodsNum:0,
        goodsNumMax:0,
			  goodsList:[],
        evaluateList:[],
        userId:localStorage.getItem('userId'),
			}
		},
    mounted() {
      this.goodsList = this.$store.state.goodsInfo;
      console.log(this.goodsList);
      this.getGoodsInfo();
      this.getEvaluateList();
    },
		methods: {
      tel(telNum){
        dsBridge.call('dialingTel',telNum);
      },
      aaaaa(index){
        ImagePreview(
          {
            images:this.goodsInfo.goodslogo.split(',undefined')[0].split(','),
            showIndicators:true,
            startPosition:index,
            showIndex:true,
          }
        );
      },
      toSeller(dataMap){
        this.$store.commit('setSellerInfo', dataMap);
        this.$router.push('seller');
      },
      buyGoods(){
        if(this.goodsNum==0){
          this.$toast({
            message: '请选择购买数量!',
            position: "bottom"
          });
          return;
        }
        let $this = this;
        let qs = require('qs');
        let data = {
          goodsid:this.goodsList.goodsid,
          userid:this.userId,
          num:this.goodsNum,
        };
        $this.$ajax.post("order/addorder", qs.stringify(data), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            // $this.$toast({
            //   message: '购买成功,请进入订单付款!',
            //   position: "bottom"
            // });
            // $this.$router.back(-1);
            $this.$store.commit('setOrderCheck', rpdata.data.data);
            $this.$router.push('orderCheck');
            // $this.$router.push({path:'orderCheck',query:{data:rpdata.data.data}});
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      minusNum(){
        if(this.goodsNum==0){
          this.$toast({
            message: '购买数量不得为负数!',
            position: "bottom"
          });
        }else{
          this.goodsNum--;
        }
      },
      addNum(){
        if(this.goodsNum==this.goodsNumMax){
          this.$toast({
            message: '库存上限!',
            position: "bottom"
          });
        }else{
          this.goodsNum++;
        }
      },
      removeCollect(){
        let $this = this;
        let qs = require('qs');
        let data = {
          goodsid:this.goodsList.goodsid,
          userid:this.userId,
        };
        $this.$ajax.post("collection/canclecollectiono", qs.stringify(data), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.goodsInfo.iscollection = 'N';
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      addCollect(){
        let $this = this;
        let qs = require('qs');
        let data = {
          goodsid:this.goodsList.goodsid,
          userid:this.userId,
        };
        $this.$ajax.post("collection/addcollection", qs.stringify(data), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.goodsInfo.iscollection = 'Y';
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
      getEvaluateList(){
        let $this = this;
        let qs = require('qs');
        let data = {
          goodsid:this.goodsList.goodsid,
          userid:this.userId,
        };
        $this.$ajax.post("evaluate/getevaluatlist", qs.stringify(data), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.evaluateList = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
      getGoodsInfo(){
        let $this = this;
        let qs = require('qs');
        let data = {
          goodsid:this.goodsList.goodsid,
          userid:this.userId,
        };
        $this.$ajax.post("goods/getgoodsinfo", qs.stringify(data), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.data){
            $this.goodsInfo = rpdata.data.data;
            $this.goodsNumMax = rpdata.data.data.num;
          }
        })
        .catch(function(err) {})
      },
      copeText(){
        dsBridge.call('shareUrl','https://www.pgyer.com/DRwB');
      }
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .minusNum {
    border: 1px solid #8b8b8b;
    padding: 0 6px;
    border-radius: 10px;
  }
  .addNum {
    border: 1px solid #8b8b8b;
    padding: 0 4px;
    border-radius: 10px;
  }
  .goods-bottom {
    padding-bottom: 3px;
    width: 23px;
    padding-left: 30%;
  }
  .goods-title {
    text-align: right;
    font-weight: bold;
    font-size: 16px;
  }
	#goods {
		padding-bottom: 50px;
	}
</style>



